.App {
  width: 100%;
  height: auto;
  display: flex;
  flex-direction: column;
  font-family: 'Roboto', sans-serif;
}

body {
  padding: 0;
  margin: 0;
}

.Salary {
  max-width: 80%;
  margin: auto;
 box-shadow: rgb(179, 160, 110);
  text-align: center;
}

.main {
  margin-top: 5em;
  background-color: rgb(191, 224, 211);
  font-family:-apple-system, BlinkMacSystemFont,
   'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}
.center {
  justify-content: center;
  padding: 10px;
  background-color:rgb(191, 224, 211) ;
box-shadow: black;
}

.ui.search input {
  width: 100%;
  border-radius: 0 !important;

}
.item {
  padding: 15px 0px !important;
}
i.icon {
  float: right;
  font-size: 21px;
  cursor:pointer;

}

.invalid-field {
  border-color: #dc3545;
  color:#dc3545;
  background-color: #fff;
}

table div.card {
  align-items: center;
  border: none;
  transition: transform 0.2s;
}
table div.card:hover {
  cursor: pointer;
  transform: scale(1.05);
  box-shadow: 0 0 10px #333;
  z-index: 10;
}

table div.card:hover button.delete-button {
  visibility: visible;

}

table div.card-body {
  text-align: center;
}

table div.card img {
  width: 175px;
  height: 175px;
  margin: 30px;
}

table div.card .delete-button {
  visibility: hidden;
}

table tr td:not(:nth-child(3)){
  border-right: 1px solid #00000020;
  
}

table tr:not(:last-child) td{
  border-bottom: 1px solid #00000020;
}


.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}
.btn-info {
  color: #fff;
  background-color: #FFA500;
  border-color: #FFA500;
}
.btn-danger {
  color: #fff;
  background-color: #000000;
  border-color: #000000;
  background: #000000;
}
.btn-primary {
  color: #fff;
  background-color: #FFA500;
  border-color: #FFA500;
}

.App-link {
  color: #fbd261;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
